import axios from 'axios'

const itinerActions = {
    getItineraries: idofCity => {
        return async (dispatch, getState) => {
            const response = await axios.get(`https://myitineraryivancamperchioli.herokuapp.com/api/itineraries/${idofCity}`)
                dispatch({
                    type: 'GET_ITINERARIES',   
                    payload: response.data.itineraries  
                })    
        }
    },

    getCities: idofCity => {
        return async (dispatch, getState) => {
            const response = await axios.get(`https://myitineraryivancamperchioli.herokuapp.com/api/cities/${idofCity}`)
            dispatch({
                type: 'GET_CITIES',   
                payload: [response.data.imgofCity]  
            })    
        }
    },

    loadComments: (comment, idItinerary) => {
        return async (dispatch, getState) => {
            const response = await axios.put(`https://myitineraryivancamperchioli.herokuapp.com/api/itineraries/${idItinerary}`, 
            {coments: comment})

            dispatch({
                type: 'GET_ITINERARIES',
                payload: response.data.comment
            })
        }
    },

    removeComent: (commentRemove) => {
        return async (dispatch, getState) => {
            const response = await axios.delete(`https://myitineraryivancamperchioli.herokuapp.com/api/comments/${commentRemove.map(coment => {return coment.coments})}`)
            console.log(commentRemove.map(coment => {return coment.coments}))
        } 

    }
}

export default itinerActions
