import axios from 'axios'

const userAction = {
    createAccount: newUser => {
        return async (dispatch, getState) => {
            const response = await axios.post('https://myitineraryivancamperchioli.herokuapp.com/api/user', newUser)
            if (!response.data.success) {
                alert(response.data.error)
            } else {
                dispatch({
                    type: 'LOG_USER',   
                    payload: {  token: response.data.token,
                                firstName: response.data.firstName,
                                urlPick: response.data.urlPick
                    }
                })    
            }
        }
    },

    userLogIn: user => {
        return async (dispatch, getState) => {
            const response = await axios.post('https://myitineraryivancamperchioli.herokuapp.com/api/login', user)
            if (!response.data.success) {
                alert(response.data.error)
            } else {
                dispatch({
                    type: 'LOG_USER',
                    payload: {  token: response.data.token, 
                                firstName: response.data.firstName,
                                urlPick: response.data.urlPick
                    }
                })
            }
        }
    },

    unlog: () => {
        return (dispatch, getState) => {
            dispatch({
                type: 'UNLOG_USER'
            })
        }
    },

    localStorageLogin: tokenLS => {
        return async (dispatch, getState) => {
            const response = await axios.get('https://myitineraryivancamperchioli.herokuapp.com/api/tokenLS', {
                headers: {
                    Authorization: `Bearer ${tokenLS}`
                }
            })
            if (response.data.success) {
                dispatch({
                    type: 'LOG_USER',
                    payload: {  firstName: response.data.firstName,
                                urlPick: response.data.urlPick,
                                token: tokenLS
                    }
                })
            }
        }
    }
}

export default userAction