import axios from 'axios'

const citiesActions = {
    getCities: () => {
        return async (dispatch, getState) => {
            const response = await axios.get('https://myitineraryivancamperchioli.herokuapp.com/api/cities')
            const citiesOrder = response.data.cities 
            dispatch({
                type: 'GETCITIES',
                payload: citiesOrder  
            })
        }
    },
    filterCities: (filter) => {
        return async (dispatch, getState) => {
            dispatch({
                type: 'FILTEREDCITIES',
                payload: filter
            })
        }
    },
}

export default citiesActions